import axios from 'axios';
import { isLoggedIn } from '../auth';

export function getSkillsetsList(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/skillsets`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function getSkillset(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/skillsets/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function createSkillset(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/skillsets', data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function updateSkillset(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/skillsets/${id}`, data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
